.contacts {
  padding: 64px;

  background-image: url("../img/feedback.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 16px;
}

.contacts-container {
  width: 343px;
  padding: 40px;

  background-color: #fcfcfc;
  border-radius: 8px;
  box-shadow: 0 8px 16px rgba(45, 52, 64, 0.12);
}

.contacts-text {
  margin: 0;

  color: #565c66;
}

.contacts-address {
  margin: 13px 0 24px;

  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
}

.contacts-phone {
  display: inline-block;
  margin-top: 13px;
  // font-style: normal;

  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #2d3440;
  text-decoration: none;
}

.contacts-text-small {
  margin: 0;

  font-size: 14px;
  line-height: 20px;
  color: #565c66;
}

.contacts .button {
  width: 250px;
  margin-top: 32px;
}
