.present .title-block {
  max-width: 434px;
  margin: 0 auto 53px;
}

.present-list {
  display: grid;

  gap: 30px;
  grid-template-columns: repeat(2, 1fr);
}

.present-item {
  min-height: 268px;
  padding: 18px 210px 18px 36px;

  background-color: #ff7799;
  border-radius: 16px;

  &-raspberry {
    background-image: url("../../img/present-raspberry.png");
    background-repeat: no-repeat;
    background-position: bottom right;
  }

  &-marshmallow {
    background-image: url("../img/present-marshmallow.png");
    background-repeat: no-repeat;
    background-position: bottom right;
  }
}

.present-title {
  margin-bottom: 26px;

  font-weight: 700;
  font-size: 24px;
  line-height: 30px;
}

.present-description {
  margin-bottom: 29px;
}

.present .button {
  width: 176px;
}
