.navigation {
  display: flex;
  align-items: center;
  padding: 24px 0;
}

.navigation-logo {
  display: block;
  width: 137px;
  height: 56px;
  margin-right: 15px;
}

.navigation-logo img {
  display: block;
  width: 100%;
  height: 100%;
}

.navigation-list {
  display: grid;

  grid-template-columns: repeat(3, auto);
}

.navigation-item {
  position: relative;

  padding: 5px 0;
}

.navigation-link {
  display: inline-block;
  padding: 7px 15px;

  font-size: 16px;
  line-height: 20px;
  vertical-align: baseline;
  color: #2d3440;
  text-decoration: none;

  border: 2px solid transparent;
  border-radius: 30px;

  &:hover {
    background-color: rgba(252, 252, 252, 0.5);
  }

  &:focus {
    background-color: rgba(252, 252, 252, 0.5);
    border: 2px solid #2d3440;
    outline: none;
  }

  &:active {
    background-color: #fcfcfc;
    border: 2px solid transparent;
  }
}

.navigation-link-current {
  color: #fcfcfc;

  background-color: #ff4a78;
}

.navigation-list-drop {
  position: absolute;
  top: 100%;
  z-index: 10;

  display: none;
  width: 135px;
  padding: 16px 0 7px;

  background-color: #fcfcfc;
  border-radius: 4px;
  box-shadow: 0 8px 16px rgba(45, 52, 64, 0.12);
}

.navigation-item-drop:hover .navigation-list-drop,
.navigation-item-drop:focus-within .navigation-list-drop {
  display: block;
}

.navigation-drop-title {
  margin: 0 16px;
  padding: 0 0 13px;

  font-size: 14px;
  line-height: 20px;

  border-bottom: 1px solid #b9b9b9;
}

.navigation-drop-list {
  padding-top: 6px;
}

.navigation-drop-item {
  margin-bottom: 4px;
}

.navigation-drop-link {
  display: block;
  padding: 4px 10px 4px 18px;

  font-size: 14px;
  line-height: 20px;
  color: #2d3440;
  text-decoration: none;

  &:hover {
    background-color: #ffcbd8;
  }

  &:active,
  &:focus {
    background-color: #ff7799;
    outline: none;
  }
}

.navigation-drop-link-current {
  color: #fcfcfc;

  background-color: #ff4a78;
}

.navigation-user-link-phone {
  display: inline-block;
  margin-left: auto;

  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  vertical-align: baseline;
  color: #2d3440;
  text-decoration: none;
}

.navigation-user-link-phone:hover,
.navigation-user-link-phone:focus {
  color: #000000;

  outline: none;
}

.navigation-user-link-phone:active {
  opacity: 0.5;
}

.navigation-user {
  display: grid;
  align-items: center;

  grid-template-columns: repeat(4, auto);
}

.navigation-user-item {
  position: relative;

  margin-left: 16px;
  padding: 8px 0;
}

.navigation-user-link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4px 13px;

  font-size: 14px;
  line-height: 20px;
  color: #2d3440;
  text-decoration: none;

  background-color: rgba(252, 252, 252, 0.3);
  border: 2px solid transparent;
  border-radius: 30px;
}

.navigation-user-link:hover {
  background-color: rgba(252, 252, 252, 0.5);
}

.navigation-user-link:focus {
  background: rgba(252, 252, 252, 0.5);
  border: 2px solid #000000;
  outline: none;
}

.navigation-user-link:active {
  background-color: #fcfcfc;
  border: 2px solid transparent;
}

.navigation-user-link span {
  margin-left: 9px;
}

.navigation-user-link-search {
  width: 32px;
  height: 32px;
  padding: 0;
}

.navigation-user-link-current,
.navigation-user-link-current:focus {
  color: #fcfcfc;

  background-color: #ff4a78;
  border: 2px solid transparent;
}

.navigation-user-container {
  position: absolute;
  top: 100%;
  right: 0;
  z-index: 10;

  display: none;
  width: 356px;
  padding: 24px 16px;

  background-color: #fcfcfc;
  border-radius: 4px;
}

.navigation-user-container-open {
  display: block;
}

.navigation-user-title {
  margin-bottom: 30px;

  font-size: 24px;
  line-height: 30px;
}
