.product {
  padding-top: 5px;
}

.product-page {
  padding-top: 28px;
  padding-bottom: 25px;

  border-bottom: 1px solid rgba(252, 252, 252, 0.3);
}

.product .title-block {
  max-width: 527px;
  margin: 0 auto 53px;
}

.product-list {
  display: grid;

  gap: 38px 30px;
  grid-template-columns: repeat(4, 1fr);
}

.product-item {
  padding-top: 53px;

  text-align: center;
}

.product-wrapper {
  // display: flex;
  // flex-direction: column;
  height: 100%;
  padding: 25px 40px;

  background-color: #fcfcfc;
  border-radius: 8px;
}

.product-img {
  width: 168px;
  height: 168px;
  margin: -75px auto 16px;
}

.product-img img {
  display: block;
  width: 100%;
  height: 100%;

  border-radius: 50%;
}

.product-title {
  margin: 0;

  font-size: 20px;
  line-height: 24px;
}

.product-descript {
  margin-top: 8px;
  margin-bottom: 14px;
}

.product-card-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
}

.product-price {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
}

.product-card {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;

  color: #fcfcfc;

  background-color: #ff2f64;
  border: 2px solid transparent;
  border-radius: 50%;

  &:hover {
    color: #000000;

    background-color: rgba(252, 252, 252, 0.3);
    border: 2px solid #ff2f64;
  }

  &:active {
    color: #000000;

    background-color: #ffcbd8;
    border: 2px solid #ff2f64;
  }

  &:focus {
    color: #fcfcfc;

    background-color: #ff2f64;
    border: 2px solid #2d3440;
    outline: none;
  }

  &:disabled {
    color: #565c66;

    background-color: #b9b9b9;
  }
}
