.filter {
  padding-top: 39px;
}

.filter-form {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
}

.filter-fieldset {
  display: flex;
  flex-direction: column;
  margin: 0 24px 15px 0;
  padding: 0;

  font-weight: 400;
  font-size: 14px;
  line-height: 20px;

  border: none;
}

.filter-title {
  margin: 0 0 7px;
  padding-left: 15px;

  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
}

.filter-sorting {
  width: 196px;
  height: 36px;
  padding: 0 40px 0 14px;

  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  font-family: $font;
  color: #2d3440;

  background: rgba(252, 252, 252, 0.3);
  background-image: url("../img/icon-arrow-select.svg");
  background-repeat: no-repeat;
  background-position: center right 11px;
  border: 2px solid transparent;
  border-radius: 26px;

  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

.filter-sorting:hover {
  background-color: rgba(252, 252, 252, 0.5);
}

.filter-sorting:disabled {
  background-color: rgba(0, 0, 0, 0.4);
  border: 2px solid rgba(0, 0, 0, 0.4);
}

.filter-sorting:focus {
  background-color: rgba(252, 252, 252, 0.4);
  border: 2px solid #fcfcfc;
  outline: none;
}

.range-wrapper-inputs {
  display: flex;
}

.range-input {
  width: 30px;
  margin: 0 -4px 0 -2px;
  padding: 0;

  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  font-family: $font;
  text-align: center;
  color: #2d3440;

  background-color: transparent;
  border: none;

  appearance: textfield;
}

.range-input::-webkit-outer-spin-button,
.range-input::-webkit-inner-spin-button {
  margin: 0;

  appearance: none;
}

.range-wrapper-inputs span {
  margin: 0 5px;
}

.range {
  position: relative;

  width: 196px;
  height: 36px;
  padding: 10px 16px;

  background: rgba(252, 252, 252, 0.3);
  border-radius: 20px;
}

.range-scale {
  position: relative;
  top: 50%;
  left: 50%;

  width: 164px;
  height: 2px;

  background-color: rgba(86, 92, 102, 0.3);
  transform: translate(-50%, -50%);
}

.range-bar {
  position: absolute;
  top: 50%;
  right: 52px;
  left: 24px;

  height: 2px;

  background-color: #565c66;
  transform: translateY(-50%);
}

.range-toggle {
  position: absolute;
  top: 50%;

  width: 16px;
  height: 16px;

  background-color: #2d3440;
  border: none;
  border-radius: 50%;
  transform: translateY(-50%);
  cursor: pointer;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;

    width: 4px;
    height: 4px;

    background-color: #fcfcfc;
    border-radius: 50%;
    transform: translate(-50%, -50%);
  }
}

.range-toggle:hover {
  background-color: #fcfcfc;
}

.range-toggle:hover::after {
  background-color: #2d3440;
}

.range-toggle:focus {
  background-color: #fcfcfc;
  outline: 2px solid #2d3440;
}

.range-toggle:focus::after {
  background-color: #2d3440;
}

.range-toggle:active {
  background: #2d3440;
  outline: none;
}

.range-toggle:active::after {
  background-color: #2d3440;
}

.range-toggle-min {
  left: 0;
}

.range-toggle-max {
  right: 0;
}

.filter-fat-list {
  display: flex;
  flex-wrap: wrap;
  padding: 10px 24px 8px;

  background-color: rgba(252, 252, 252, 0.3);
  border-radius: 18px;
}

.filter-fat-item:not(:last-child) {
  margin-right: 16px;
}

.filter-label {
  position: relative;
}

.filter-label-radio {
  padding-left: 25px;
}

.filter-label-radio::before,
.filter-label-radio::after {
  content: "";
  position: absolute;

  border-radius: 50%;
}

.filter-label-radio::before {
  top: 0;
  left: 0;

  width: 16px;
  height: 16px;

  background-color: transparent;
  border: 1px solid #2d3440;
}

.filter-label-radio::after {
  top: 5px;
  left: 5px;

  display: none;
  width: 6px;
  height: 6px;

  background-color: #2d3440;
}

// Выбранный элемент
.filter-fat-input:checked ~ .filter-label-radio::after {
  display: block;
}

// Наведение
.filter-label-radio:hover::before {
  border: 1px solid #fcfcfc;
  outline: none;
}

.filter-label-radio:hover::after {
  background-color: #fcfcfc;
  outline: none;
}

// Фокус
.filter-fat-input:focus ~ .filter-label-radio::before {
  border: 1px solid #fcfcfc;
  outline: none;
}

.filter-fat-input:focus ~ .filter-label-radio::after {
  background-color: #fcfcfc;
  outline: none;
}

// Нажатие
.filter-label-radio:active::before {
  border: 1px solid rgba(0, 0, 0, 0.4);
}

.filter-label-radio:active::after {
  background-color: rgba(0, 0, 0, 0.4);
}

// disabled
.filter-fat-input:disabled ~ .filter-label-radio::before {
  border: 1px solid rgba(0, 0, 0, 0.4);
}

.filter-fat-input:disabled ~ .filter-label-radio::after {
  background-color: rgba(0, 0, 0, 0.4);
}

.filter-fillers-list {
  display: flex;
  flex-wrap: wrap;
  padding: 11px 24px 9px;

  background: rgba(252, 252, 252, 0.3);
  border-radius: 18px;
}

.filter-fillers-item:not(:last-child) {
  margin-right: 16px;
}

.filter-label-check {
  padding-left: 25px;
}

.filter-label-check::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;

  display: block;
  width: 16px;
  height: 16px;
  margin-right: 8px;

  background-color: #2d3440;

  -webkit-mask-image: url("../img/icon-check.svg");
          mask-image: url("../img/icon-check.svg");
}

// Выбранный элемен
.filter-fillers-input:checked ~ .filter-label-check::after {
  -webkit-mask-image: url("../img/icon-check-checked.svg");
          mask-image: url("../img/icon-check-checked.svg");
}

// Наведение
.filter-label-check:hover::after {
  background-color: #fcfcfc;
}

// Фокус
.filter-fillers-input:focus ~ .filter-label-check::after {
  background-color: #fcfcfc;
}

.filter-fillers-input:focus ~ .filter-label-check:active::after {
  background-color: rgba(45, 52, 64, 0.5);
}

// Нажатие
.filter-label-check:active::after {
  background-color: rgba(45, 52, 64, 0.5);
}

// disabled
.filter-fillers-input:disabled ~ .filter-label-check::after {
  background-color: rgba(45, 52, 64, 0.5);
}

.filter .button {
  width: 185px;
  margin-bottom: 15px;
  margin-left: -8px;
  padding: 7px;

  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
}
