@keyframes slide-down {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }

  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.slider {
  position: relative;
}

.slider-container {
  display: grid;
  padding-top: 20px;

  grid-template-columns: 540px auto;
}

.slider-item {
  display: none;
  width: 540px;
  padding: 86px 30px 115px 70px;
}

.slider-item-active {
  display: block;
}

.slider-title {
  margin-bottom: 25px;

  font-weight: 900;
  font-size: 36px;
  line-height: 46px;
}

.slider-item p {
  width: 414px;
  margin-bottom: 47px;

  font-size: 18px;
  line-height: 24px;
}

.slider .button {
  width: 139px;
}

.slider-thumbnail {
  display: grid;
  align-items: center;

  gap: 40px;
  grid-template-columns: 350px 100px 100px;
}

.slider-thumbnail-item {
  position: relative;

  width: 100px;
  height: 150px;

  opacity: 0.4;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: -1;

    width: 100px;
    height: 100px;

    background-color: #ffcbd8;
    border-radius: 50%;
    transform: translate(-50%, -50%);
  }
}

.slider-thumbnail-item img {
  display: block;
  width: auto;
  height: 100%;
  margin: 0 auto;
}

.slider-thumbnail-item-active {
  width: 350px;
  height: 507px;
  padding: 0 20px;

  opacity: 1;

  &::before {
    width: 312px;
    height: 312px;

    opacity: 1;
  }
}

.slider-bullet {
  width: 12px;
  height: 12px;
  margin-right: 4px;
  padding: 0;

  background-color: rgba(252, 252, 252, 0.3);
  border: 1px solid transparent;
  border-radius: 50%;
  cursor: pointer;

  &:hover {
    border: 1px solid #fcfcfc;
  }

  &:focus {
    border: 1px solid #2d3440;
    outline: none;
  }

  &:active {
    background-color: rgba(252, 252, 252, 0.5);
  }
}

.slider-bullet-active {
  background-color: #fcfcfc;
}

.slider-control {
  position: relative;
}

.slider-navigation-arrow {
  position: absolute;
  top: 50%;
  left: 0;

  display: flex;
  justify-content: space-between;
  width: 350px;

  transform: translateY(-50%);
}

.slider-arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 38px;
  height: 38px;

  color: #fcfcfc;

  background-color: rgba(252, 252, 252, 0.3);
  border: 2px solid #fcfcfc;
  border-radius: 50%;
}

.slider-arrow:hover {
  color: #2d3440;

  background-color: #fcfcfc;
}

.slider-arrow:active {
  color: #2d3440;

  background-color: rgba(252, 252, 252, 0.5);
}

.slider-arrow:focus {
  color: #2d3440;

  background-color: rgba(252, 252, 252, 0.3);
  border: 2px solid #2d3440;
  outline: none;
}

.slider-arrow:disabled {
  color: #fcfcfc;

  background: rgba(252, 252, 252, 0.3);
  border: 2px solid #fcfcfc;
  opacity: 0.4;
}

.slider-arrow-prev {
  transform: rotate(180deg);
}

.slider-social {
  position: absolute;
  right: 0;
  bottom: 0;
}
