.promo {
  display: grid;

  color: #565c66;

  gap: 30px;
  grid-template-columns: repeat(2, 1fr);
}

.promo-blog {
  height: 100%;
  padding: 33px 220px 33px 32px;

  background-color: #ffffff;
  background-image: url("../img/promo-blog.png");
  background-repeat: no-repeat;
  background-position: left center;
  // background-position: -8px -105px;
  // background-size: 115%;
  background-size: cover;
  border-radius: 16px;
}

.promo-text {
  margin: 0 0 13px;

  font-size: 16px;
  line-height: 20px;
  color: #565c66;
}

.promo-link {
  font-weight: 700;
  font-size: 24px;
  line-height: 30px;
  color: #2d3440;
  text-decoration: none;
}

.promo-subscription {
  padding: 6px;

  background-image: url("../img/promo-subcript.png");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 16px;
}

.promo-subscription-wrapper {
  padding: 26px 26px 28px;

  background-color: #ffffff;
  border-radius: 16px;
}

.promo-subscription-descript {
  margin-top: 0;
  margin-bottom: 44px;
}

.promo-subscription-form {
  display: grid;

  gap: 22px;
  grid-template-columns: 1fr auto;
}

.promo .button {
  width: 152px;
}
