.modal-container {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 5;

  display: none;
  width: 100%;
  height: 100%;

  background-color: rgba(0, 0, 0, 0.3);
}

.modal-container-open {
  display: flex;
}

.modal {
  position: relative;

  margin: auto;
  padding: 40px;

  background: #fcfcfc;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 15px 40px rgba(45, 52, 64, 0.12);
}

.modal-auth {
  width: 570px;
  padding: 49px 64px 70px;
}

.modal-close-button {
  position: absolute;
  top: 27px;
  right: 27px;

  width: 30px;
  height: 30px;
  padding: 0;

  background-color: #ffffff;
  background-image: url("../img/icon-close.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 12px 12px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
}

.modal-form {
  display: grid;
  padding-top: 15px;

  gap: 16px;
  grid-template-rows: 1fr;
}

.modal-form textarea {
  height: 146px;
  padding-top: 13px;

  resize: none;
}

.modal-content .button-other {
  margin-top: 16px;
  margin-left: auto;
}
