.advantage {
  padding: 24px;

  background-color: #f0e4cb;
  background-image: url("../img/advantage-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 16px;
}

.advantage-wrapper {
  padding: 56px 40px 58px;

  background-color: #ffffff;
  border-radius: 16px;
}

.advantage .title-block {
  margin-bottom: 55px;
}

.advantage-list {
  display: grid;

  gap: 38px 30px;
  grid-template-columns: repeat(2, 1fr);
}

.advantage-item {
  min-height: 48px;
  padding-left: 64px;

  color: #565c66;

  background-repeat: no-repeat;
  background-position: top left;
  background-size: 48px 48px;
}

.advantage-item-production {
  background-image: url("../img/icon-production.svg");
}

.advantage-item-ingredients {
  background-image: url("../img/icon-ingredients.svg");
}

.advantage-item-quality {
  background-image: url("../img/icon-quality.svg");
}

.advantage-item-delivery {
  background-image: url("../img/icon-delivery.svg");
}
