.delivery {
  display: grid;
  padding: 64px;

  column-gap: 92px;

  background-color: #ffffff;
  background-image: url("../img/delivery.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 16px;

  grid-template-columns: auto auto;
}

.delivery-descript {
  width: 450px;
  padding: 104px 36px 10px;
}

.delivery .title-block {
  margin-bottom: 23px;

  text-align: left;
}

.delivery-form {
  padding: 25px 40px 42px;

  background-color: #fcfcfc;
  border-radius: 8px;
  box-shadow: 0 15px 40px rgba(45, 52, 64, 0.12);
}

.delivery-form form {
  display: grid;
  padding-top: 15px;

  gap: 20px 16px;
  grid-template-columns: 117px 1fr;
}

.delivery-form-label {
  display: inline-block;
  margin-bottom: 7px;

  font-weight: 700;
  line-height: 20px;
}

.delivery-form-container-address {
  grid-column: 1/-1;
}

.delivery-form textarea {
  padding: 12px 15px;

  resize: none;
}

.delivery-form .button {
  margin: 6px auto 0;

  grid-column: 1/-1;
}

.delivery-form-tooltip {
  position: relative;

  display: inline-block;
}

.delivery-tooltip {
  position: absolute;
  top: 4px;
  right: 0;
}

.delivery-tooltip-toggle {
  position: absolute;
  top: 0;
  left: 6px;

  width: 12px;
  height: 12px;

  background-color: transparent;
  background-image: url("../img/icon-info.svg");
  background-repeat: no-repeat;
  background-position: center;
  border: none;
}

.delivery-tooltip-text {
  position: absolute;
  top: -18px;
  left: 30px;

  display: none;
  width: 150px;
  margin: 0;
  padding: 10px 8px 10px 15px;

  font-size: 12px;
  line-height: 16px;
  color: #565c66;

  background-color: #fcfcfc;
  border: 1px solid #b9b9b9;
  border-radius: 1px;
  box-shadow: 0 2px 12px rgba(173, 152, 143, 0.18);

  &::after {
    content: "";
    position: absolute;
    top: 16px;
    left: -8px;

    width: 14px;
    height: 14px;

    background: #fcfcfc;
    border: 1px solid #b9b9b9;
    border-top: none;
    border-right: none;
    transform: rotate(45deg);
  }
}

.delivery-tooltip-toggle:hover ~ .delivery-tooltip-text {
  display: block;
}
