.login {
  width: 420px;
  padding: 30px 48px 48px;
}

.login .form-input {
  margin-bottom: 16px;
}

.login-wrapper {
  display: grid;
  padding-top: 15px;

  gap: 20px;
  grid-template-columns: repeat(2, auto);
}

.login .button {
  width: 113px;
}

.login-link {
  display: inline-block;

  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  vertical-align: baseline;
  color: #2d3440;
  text-decoration: none;

  border-bottom: 1px solid #2d3440;

  &:hover,
  &:focus {
    color: #000000;

    outline: none;
  }

  &:active {
    opacity: 0.5;
  }
}
