*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;

  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: "Inter", sans-serif;
  color: #2d3440;

  background-color: #feafc3;
}

img {
  max-width: 100%;

  object-fit: cover;
  object-position: center;
}

.main {
  flex-grow: 1;
}

.main-container {
  display: grid;

  grid-template-columns: 1fr;
  row-gap: 80px;
}
