.card {
  width: 441px;
  padding: 30px 48px 48px;
}

.card-list {
  padding-bottom: 5px;

  border-bottom: 1px solid #e7e7e7;
}

.card-item {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
}

.card-img {
  width: 46px;
  height: 46px;
  margin-right: 16px;
}

.card-title {
  display: block;
  margin-bottom: 3px;

  font-size: 18px;
  line-height: 22px;
  color: #2d3440;
  text-decoration: none;

  &:hover,
  &:focus {
    color: #000000;

    outline: none;
  }

  &:active {
    opacity: 0.5;
  }
}

.card-descript-text {
  color: #b9b9b9;
}

.card-price {
  display: inline-block;
  margin: 0 57px 0 auto;

  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  vertical-align: middle;
}

.card-delete {
  width: 10px;
  height: 10px;

  background-color: transparent;
  background-image: url("../img/icon-delete.svg");
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;

  &:hover,
  &:focus {
    outline: none;
    opacity: 0.8;
  }

  &:active {
    opacity: 0.5;
  }
}

.card-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 32px;
}

.card .button {
  width: 198px;
}

.card-total {
  display: inline-block;

  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  vertical-align: middle;
}

.card-empty {
  width: 288px;

  font-weight: 700;
  font-size: 24px;
  line-height: 30px;
  text-align: center;
}
