.form-input {
  width: 100%;
  height: 48px;
  padding: 5px 15px;

  font-size: 16px;
  line-height: 20px;
  font-family: $font;
  color: #2d3440;

  background-color: #ffffff;
  border: 1px solid #b9b9b9;
  border-radius: 4px;
}

.form-input::placeholder {
  color: #b9b9b9;
}

.form-input:hover {
  border: 1px solid #2d3440;
}

.form-input:focus {
  border: 1px solid #2d3440;
  outline: 1px solid #2d3440;
}

.form-input-error {
  border: 1px solid #ea5454;

  &:hover {
    border: 1px solid #ea5454;
    outline: 1px solid #ea5454;
  }
}

.form-input:disabled {
  color: rgba(45, 52, 64, 0.65);

  background-color: #e7e7e7;

  &:hover {
    color: #2d3440;

    border: 1px solid #b9b9b9;
  }
}
